.home {
  height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.home__container {
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.home__title {
  width: 308px;
  height: 219px;

  margin-bottom: 74px;
}

@media (min-width: 960px) {
  .home__title {
    margin-bottom: 26px;
  }
}

.home__button {
  width: 322px;
  height: 87px;
}

@media (min-width: 960px) {
  .home__button {
    width: 336px;
  }
}

.home__hands {
  width: 375px;
  height: 131px;

  display: flex;
  justify-content: center;
  column-gap: 45px;

  overflow: hidden;
}

@media (min-width: 960px) {
  .home__hands {
    width: 488px;
    height: 185px;

    column-gap: 65px;
  }
}
